import React, { Component } from "react";
import { Link } from "react-router-dom";
import logo from "../images/tribar-sm.png";

class NavBar extends Component {
  render() {
    return (
      <div>
        <nav>
          <div className="navbarContainer">
            <div onClick={ this.props.onHomeClicked }>
              <Link to={{ pathname: "/" }}> <img src={logo} alt="app logo" className="logo"/> </Link>
            </div>
            <div className="navLinkContainer" onClick={ this.props.onAboutClicked }>
              <Link to={{ pathname: "/about" }} className={(this.props.menuNumber === 1) ? "navLink textEnlighted" : "navLink"}> About </Link>
            </div>
            <div className="navLinkContainer" onClick={ this.props.onProjectsClicked }>
              <Link to={{ pathname: "/projects" }} className={(this.props.menuNumber === 2) ? "navLink textEnlighted" : "navLink"} id="projectsLink"> Gallery </Link>
            </div>
            <div className="navLinkContainer" onClick={ this.props.onContactClicked }>
              <Link to={{ pathname: "/contact" }} className={(this.props.menuNumber === 3) ? "navLink textEnlighted" : "navLink"}> Contact </Link>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default NavBar;